import React from "react";

const CommentsList = ({ comments }) => (
  <>
    <h4>Comments</h4>
    {comments.map((comment, key) => {
      if (comment.comment) {
        return (
          <div key={key} className="comment">
            {comment.username ? <h4>{comment.username}</h4> : <h4>Unknown</h4>}
            <p>{comment.comment}</p>
          </div>
        );
      }

      return null;
    })}
  </>
);

export default CommentsList;
