import React from "react";
import { useState, useEffect } from "react";
import { useParams } from "react-router";
import articles from "../article-content";
import ItemList from "../Components/ItemList";
import CommentsList from "../Components/CommentsList";
import NotFoundPage from "./NotFoundPage";
import UpvoteSection from "../Components/UpvotesSection";
import CommentForm from "../Components/CommentForm";

const ArticlePage = () => {
  const name = useParams().name;
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
  // fetch data from MongoDB and set the state
  // TODO: include article body as well !!!
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
  }, [name]);

  // TODO: remove this
  const article = articles.filter((article) => article.name === name)[0];
  if (!article) {
    return <NotFoundPage />;
  } else {
    const otherArticles = articles.filter((article) => article.name !== name);
    // console.log(otherArticles);
    return (
      <>
        <h1>{article.title}</h1>
        <UpvoteSection
          name={name}
          upvotes={articleInfo.upvotes}
          setArticleInfo={setArticleInfo}
        />
        {article.content.map((paragraph, key) => (
          <p key={key}>{paragraph}</p>
        ))}
        <h3>Other Articles</h3>
        <ItemList items={otherArticles} />
        <CommentsList comments={articleInfo.comments} />

        <CommentForm articleName={name} setArticleInfo={setArticleInfo} />
      </>
    );
  }
};

export default ArticlePage;
